import Card from "../../components/general/card"
import SubTitling from "../../components/general/subtitling"
import Titling from "../home/titling"
import CreditsCard from "./creditsCard"
import info from './info.js'
import PolicyCard from "./policyCard"

const PrivacyPolicy = () => (
    <main>
        
        <Titling
            title={info.title}
            subtitle={info.subtitle}
        />

        <PolicyCard privacyPolicy={info.privacyPolicy} className="mt-8 max-w-md mx-auto" />

        <SubTitling
            title="Credits"
            className="mt-8"
        />

        <CreditsCard className="mt-8 max-w-xs mx-auto" />
        
    </main>
)

export default PrivacyPolicy

